body {
  background-image: url("https://thumbs.dreamstime.com/b/wood-background-wallpaper-hd-123273980.jpg");
}


.App {
  background-color: transparent;
  min-height: 800px;
  width: 600px;
  margin: 40px auto;
  border-radius: 10px;
}

#to-do-form input {
  height: 50px;
  width: 400px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  border: none;
  color: black;
}

#to-do-form input::placeholder {
  font-size: 16px;
}

#to-do-form input:focus {
  outline: none;
}

#to-do-form button {
  height: 50px;
  width: 90px;
  border-radius: 10px;
  font-weight: 800;
  background-color: royalblue;
  color: white;
  border: none;
  cursor: pointer;
}

#to-do-form button:focus {
  outline: none;
}

#to-do-form button:hover {
  background-color: green;
}