.list {
    color: black;
    width: 480px;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
    background-image: url("https://www.wallpaperup.com/uploads/wallpapers/2013/03/04/47990/0cf6d4e62aa77dffc8fe0be1946d1cf4.jpg");
    background-size: cover;
}

.list p {
    padding: 10px;
    position: relative;
    font-size: 20px;
    font-weight: 400;
}

.list p span {
    position: absolute;
    right: 10px;
}

.list p span:hover {
    color: red;
    cursor: pointer;
}

.faIcon {
    margin: 0 5px;
    font-size: 20px;
}

.tasks {
    background-color: transparent;
    border: none;
    color: black;
    font-size: 25px;
    font-weight: 800;
}

.tasks:focus {
    outline: none;
}

.resetButton {
    background-color: white;
    color: black;
    font-weight: 600;
    border: none;
    border-radius: 5px;
    height: 30px;
    margin-left: 520px;
    margin-bottom: 20px;
}

.resetButton:hover {
    cursor: pointer;
    background-color: red;
    color: white;
}

.resetButton:focus {
    outline: none;
}